import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Publishers from '@/components/Publishers/Publishers'
import PPage from '@/components/Publishers/PPage'
import Territories from '@/components/Territories/Territories'
import TPage from '@/components/Territories/TPage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/Publishers',
      name: 'Publishers',
      component: Publishers
    },
    {
      path: '/territories',
      name: 'Territories',
      component: Territories
    },
    {
      path: '/publisher/:pid',
      name: 'PPage',
      component: PPage
    },
    {
      path: '/territory/:tid',
      name: 'TPage',
      component: TPage
    },
  ]
})



