<template>
  <div>
    <h3>Dodaj głosiciela</h3>

    <div class="card addP">
      <div>
        <input
          v-model="addPData.name"
          class="validate"
          placeholder="Imię"
        >
      </div>
      <div>
        <input
          v-model="addPData.surname"
          class="validate"
          placeholder="Nazwisko"
        >
      </div>
      <div>
        <i
          @click="onAdd"
          class="material-icons"
        >
          add
        </i>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AddP',
  props: [
    'addPData',
    'onAdd'
  ]
}
</script>

<style scoped>

</style>



