<template>
  <div>
    <h3>Głosiciele</h3>

    <div
      v-for="p in sortedP"
      :key="p.pid"
      class="collection"
    >
      <div
        v-if="editId === p.pid"
        class="collection-item list-item"
        :class="{ 'yellow lighten-4': editId === p.pid }"
      >
        <div>
          <input
            class="validate"
            v-model="editPData.name"
          />
        </div>
        <div>
          <input
            class="validate"
            v-model="editPData.surname"
          />
        </div>
        <div>
          <i
            @click="onEditSubmit"
            class="material-icons"
          >
            check
          </i>
          <i
            @click="onCancel"
            class="material-icons"
          >
            cancel
          </i>
        </div>
      </div>
      <div
        v-else
        class="collection-item list-item"
      >
        <div>{{p.name}}</div>
        <div>{{p.surname}}</div>
        <div>
          <i
            @click="onEdit(p)"
            class="material-icons"
          >
            mode_edit
          </i>
          <i
            @click="onDelete(p.pid)"
            class="material-icons"
          >
            delete
          </i>
          <router-link
            :to="{
              name: 'PPage',
              params: { pid: p.pid }
            }"
          >
            <i
              class="material-icons"
            >
              search
            </i>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ListP',
  props: [
    'editId',
    'editPData',
    'onCancel',
    'onDelete',
    'onEdit',
    'onEditSubmit',
    'sortedP'
  ]
}
</script>

<style scoped>

</style>



