<template>
  <div>
    <div v-if="loaded">
      <h3>{{name}} {{surname}}</h3>
      <router-link :to="'/Publishers'">
        <button class="btn green">Wróć</button>
      </router-link>
    </div>
    <div v-else>
      <h3>Ładowanie....</h3>
    </div>
  </div>
</template>

<script>
import db from '@/db'

export default {
  name: 'PPage',
  data () {
    return {
      name: '',
      surname: '',
      loaded: false
    }
  },
  beforeCreate () {
    db.collection('publishers')
      .where('pid', '==', this.$route.params.pid).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const { name, surname } = doc.data()
          this.name = name
          this.surname = surname
        })
        this.loaded = true
      })
  }
}
</script>

<style scoped>

</style>



