<template>
  <div>
    <add-t
      :addTData="addTData"
      :onAdd="onAdd"
    />
    <list-t
      :editId="editId"
      :editData="editData"
      :onCancel="onCancel"
      :onDelete="onDelete"
      :onEdit="onEdit"
      :onEditSubmit="onEditSubmit"
      :sortedT="sortedT"
    />
  </div>
</template>

<script>
import db from '@/db'
import AddT from './AddT'
import ListT from './ListT'

export default {
  name: 'Territories',
  data () {
    return {
      territories: [],
      addTData: {
        tid: '',
        number: '',
        desc: '',
        free: true,
        given: [],
        recieved: []
      },
      editId: '',
      editData: {
        pid: '',
        number: '',
        desc: ''
      }
    }
  },
  components: {
    AddT,
    ListT
  },
  computed: {
    sortedT () {
      return this.territories.slice().sort((a, b) => {
        return a.number - b.number
      })
    }
    },
    created () {
      this.getT()
    },
  methods: {
    getT () {
      db.collection('territories').get().then(querySnapshot => {
        const territories = []
        querySnapshot.forEach(doc => {
          territories.push(doc.data())
        })
        this.territories = territories
      })
    },
    onAdd () {
      this.addTData.tid = new Date().getTime()
      db.collection('territories').add(this.addTData).then(this.getT)
      this.addTData = {
        tid: '',
        number: '',
        desc: '',
        free: true,
        given: [],
        recieved: []
      }
    },
    onDelete (tid) {
      db.collection('territories')
        .where('tid', '==', tid).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.delete().then(this.getT)
          })
        })
    },
    onEdit (t) {
      this.editId = t.tid
      this.editData.number = t.number
      this.editData.desc = t.desc
    },
    onCancel () {
      this.editId = ''
      this.editData.number = ''
      this.editData.desc = ''
    },
    onEditSubmit () {
      db.collection('territories')
        .where('tid', '==', this.editId).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.set({
              tid: this.editId,
              number: this.editData.number,
              desc: this.editData.desc
            }).then(this.getT)
          })
          this.onCancel()
        })
    }
  }
}
</script>

<style scoped>

</style>



