<template>
  <div>
    <div v-if="loaded">
      <h2>
        {{number}}
      </h2>
      <h3>
        {{desc}}
      </h3>
      <router-link :to="'/Territories'">
        <button class="btn green">Wróć</button>
      </router-link>
    </div>
    <div v-else>
      <h3>Ładowanie....</h3>
    </div>
  </div>
</template>

<script>
import db from '@/db'

export default {
  name: 'TPage',
  data () {
    return {
      number: '',
      desc: '',
      loaded: false
    }
  },
  beforeCreate () {
    db.collection('territories')
      .where('tid', '==', this.$route.params.tid).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const { number, desc } = doc.data()
          this.number = number
          this.desc = desc
        })
        this.loaded = true
      })
  }
}
</script>

<style scoped>

</style>



