<template>
  <div>
    <add-p
      :addPData="addPData"
      :onAdd="onAdd"
    />
    <list-p
      :editId="editId"
      :editPData="editPData"
      :onCancel="onCancel"
      :onDelete="onDelete"
      :onEdit="onEdit"
      :onEditSubmit="onEditSubmit"
      :sortedP="sortedP"
    />
  </div>
</template>

<script>
import db from '@/db'
import AddP from './AddP'
import ListP from './ListP'

export default {
  name: 'Publishers',
  data () {
    return {
      publishers: [],
      addPData: {
        pid: '',
        name: '',
        surname: ''
      },
      editId: '',
      editPData: {
        pid: '',
        name: '',
        surname: ''
      }
    }
  },
  components: {
    AddP,
    ListP
  },
  computed: {
    sortedP () {
      return this.publishers.slice().sort((a, b) => {
        return a.surname - b.surname
      })
    }
    },
    created () {
      this.getP()
    },
  methods: {
    getP () {
      db.collection('publishers').get().then(querySnapshot => {
        const publishers = []
        querySnapshot.forEach(doc => {
          publishers.push(doc.data())
        })
        this.publishers = publishers
      })
    },
    onAdd () {
      this.addPData.pid = new Date().getTime()
      db.collection('publishers').add(this.addPData).then(this.getP)
      this.addPData = {
        pid: '',
        name: '',
        surname: ''
      }
    },
    onDelete (pid) {
      db.collection('publishers')
        .where('pid', '==', pid).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.delete().then(this.getP)
          })
        })
    },
    onEdit (p) {
      this.editId = p.pid
      this.editPData.name = p.name
      this.editPData.surname = p.surname
    },
    onCancel () {
      this.editId = ''
      this.editPData.name = ''
      this.editPData.surname = ''
    },
    onEditSubmit () {
      db.collection('publishers')
        .where('pid', '==', this.editId).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.set({
              pid: this.editId,
              name: this.editPData.name,
              surname: this.editPData.surname
            }).then(this.getP)
          })
          this.onCancel()
        })
    }
  }
}
</script>

<style scoped>

</style>



