<template>
  <div>
    <h3>Tereny</h3>

    <div
      v-for="t in sortedT"
      :key="t.tid"
      class="collection"
    >
      <div
        v-if="editId === t.tid"
        class="collection-item list-item"
        :class="{ 'yellow lighten-4': editId === t.tid }"
      >
        <div>
          <input
            class="validate"
            v-model="editData.desc"
          />
        </div>
        <div>
          <i
            @click="onEditSubmit"
            class="material-icons"
          >
            check
          </i>
          <i
            @click="onCancel"
            class="material-icons"
          >
            cancel
          </i>
        </div>
      </div>
      <div
        v-else
        class="collection-item list-item"
      >
        <div>{{t.number}}</div>
        <div>{{t.desc}}</div>
        <div>
          <i
            @click="onEdit(t)"
            class="material-icons"
          >
            mode_edit
          </i>
          <i
            @click="onDelete(t.tid)"
            class="material-icons"
          >
            delete
          </i>
          <router-link
            :to="{
              name: 'TPage',
              params: { tid: t.tid }
            }"
          >
            <i
              class="material-icons"
            >
              search
            </i>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ListT',
  props: [
    'editId',
    'editData',
    'onCancel',
    'onDelete',
    'onEdit',
    'onEditSubmit',
    'sortedT'
  ]
}
</script>

<style scoped>

</style>



