<template>
  <div>
    <h3>Dodaj teren</h3>

    <div class="card addP">
      <div>
        <input
          type="number"
          v-model="addTData.number"
          class="validate"
          placeholder="Numer terenu"
        >
      </div>
      <div>
        <input
          v-model="addTData.desc"
          class="validate"
          placeholder="Opis terenu"
        >
      </div>
      <div>
        <i
          @click="onAdd"
          class="material-icons"
        >
          add
        </i>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AddT',
  props: [
    'addTData',
    'onAdd'
  ]
}
</script>

<style scoped>

</style>



